import { useEffect, useState } from 'react';
// import { ReactDOMServer } form 'react';
import './App.css';
import './Accueil.css'
import Navigation from './Navigation';
import ArtistCard from './ArtistCard';
import Accueil from './Accueil';
import Contact from './Contact';
import useContentFul from './useContentful';
import { motion, useScroll } from 'framer-motion';


function App() {

  const { scrollYProgress } = useScroll();

  const [artists, setArtists] = useState([]);
  const [homePageData, setHomePageData] = useState([]);
  const [prices, setPrices] = useState([]);
  const [drums, setDrums] = useState([]);
  const [cymbals, setCymbals] = useState([]);
  const [mics, setMics] = useState([]);
  const [divers, setDivers] = useState([]);

  const { getArtists, getHomePage, getPrices, getDrums, getCymbals, getMics, getDivers } = useContentFul();

  useEffect(() => {
    getArtists().then((response) => setArtists(response));
    getHomePage().then((response) => setHomePageData(response));
    getPrices().then((response) => setPrices(response));
    getDrums().then((response) => setDrums(response));
    getCymbals().then((response) => setCymbals(response));
    getMics().then((response) => setMics(response));
    getDivers().then((response) => setDivers(response));
  }, [getArtists, getHomePage, getPrices, getDrums, getCymbals, getMics, getDivers]);

  return (
    <div className="App">

      <section id='accueil' className="accueil-container">
        {homePageData.map((homePage, i) =>
          <Accueil data={homePage} key={i} />)}
      </section>

      <h1 id='artists' className='section-header'>Artistes</h1>
      <section className="row artists-container">
        {artists.map((artist, i) => <ArtistCard key={i} artist={artist} />)}
      </section>

      <h1 id="info" className='section-header'>Info</h1>
      <motion.section className="info-container"
        initial={{ opacity: 0 }}
        whileInView={{ opacity: 1 }}
        transition={{ duration: 0.5 }}>
        <h2>Tarifs</h2>
        <p>Prix indicatifs, sur demande un devis sera envoy. Le prix final peut diffrer du prix du devis si le travail estim lors du devis est moins consquent que le travail finalement accompli.</p>
        <table>
          {prices.map((item, i) => <tr key={i}><th>{item.service}</th><td>{item.prix}</td></tr>)}
        </table>

        <h2>Matriel</h2>
        <p>Toute demande particulire peut tre realise. Tout frais supplmentaire sera  la charge du client.</p>
        <h3>Batteries</h3>
        {drums.map((drum, i) => (
          <div key={i}>
            <h4>{drum.marque}</h4>
            <ul>
              {drum.elements.map((element, j) => <li key={j}>{element}</li>)}
            </ul>
            <p>{drum.description}</p>
          </div>
        ))}

        <h3>Cymbales</h3>
        <p>uniquement pour les pistes  la carte</p>
        {cymbals.map((cymbal, i) => (
          <div key={i}>
            <h4>{cymbal.type}</h4>
            <ul>
              {cymbal.list.map((el, j) => <li key={j}>{el}</li>)}
            </ul>
          </div>
        ))}

        <h3>Microphones</h3>
        {mics.map((mic, i) => (
          <div key={i}>
            <h4>{mic.type}</h4>
            <ul>
              {mic.modele.map((el, j) => <li key={j}>{el}</li>)}
            </ul>
          </div>
        ))}

        <h3>Divers</h3>
        {divers.map((el, i) => (
          <div key={i}>
            <h4>{el.type}</h4>
            <ul>
              {el.list.map((e, j) => <li key={j}>{e}</li>)}
            </ul>
          </div>
        ))}
      </motion.section>

      <h1 id="contact" className='section-header'>Contact</h1>
      <motion.section className="contact-container"
        initial={{ opacity: 0 }}
        whileInView={{ opacity: 1 }}
        transition={{ duration: 0.5 }}>
        <Contact />
      </motion.section>

      <Navigation />

      <motion.div
        className="progress-bar"
        style={{ scaleX: scrollYProgress }}
      />
    </div>
  );
}

// ReactDOMServer.renderToString(<App />);

export default App;