import './Contact.css';

const Contact = () => {
    return (
        <section className="container contact-container">
            <form action="/">
                <label for="name" className='col-12'> Nom<input type="text" name="name" id="name
                    " required /></label>
                <label for="email" className='col-12'> Email<input type="email" name="email" id="email" required /></label>
                <label for="message" className='col-12'> Message<textarea name="message" id="message" cols="30"
                    rows="10"></textarea></label>
                <input type="submit" id="submit" value="Envoyer" />
            </form>
        </section>
    )
};

export default Contact;